import {produce} from 'immer';
import userState, { IProp, IUserState } from 'modules/states/auth';
import * as actions from 'modules/actions/auth';
import { createContext, Dispatch, useReducer } from 'react';

export const reducer = (
  state:IUserState = userState,
  action:actions.ActionType
):IUserState => {
  switch(action.type) {
    case actions.UPDATE_USER:
      return produce(state, draft => {
        if(!action.payload) return ;
        draft.user_id = action.payload.user_id;
        draft.bookmark = action.payload.bookmark;
        draft.selected = action.payload.selected;
        draft.name = action.payload.name;
        draft.interest = action.payload.interest;
        draft.introduce = action.payload.introduce;
      });

    case actions.DELETE_USER:
      return produce(state, draft => {
        draft.user_id = "";
        draft.bookmark = [];
        draft.selected = false;
        draft.name = "";
        draft.introduce = "";
        draft.interest = "";
      });

    default :
      return state; 
    } 
};

export const authContext = createContext<{
                        state: IUserState;
                        dispatch : Dispatch<actions.ActionType>;
                      }>({state:userState, dispatch:() => null});

const Provider = ({ children }:IProp) => {
  const [state, dispatch] = useReducer(reducer, userState);
  const value = { state, dispatch };

  return (
    <authContext.Provider value={value}>
      {children}
    </authContext.Provider>
  );
}

export default Provider